body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(251, 63, 92);
  background: radial-gradient(circle, rgba(251, 63, 92, 1) 0%, rgba(177, 70, 252, 1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  max-width: 800px;
  margin: 0 auto;
  background-color: white;
  border-radius: 8px;
  padding: 12px;
  margin-top: 24px;
}

label {
  width: 100%;
  display: block;
}

button {
  background-color: #a64caf;
  border: none;
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  /* font-size: 16px; */
  margin: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #b56ebb;
}

.input-group {
  width: 50%;
  float: left;
}

.grid {
  width: 100%;
  max-width: 600px;
  display: grid;
  margin: 0 auto;
}

.row {
  width: 100%;
}

.cell {
  float: left;
  margin: 2px;
  border-radius: 4px;
  background-color: rgb(223, 223, 223);
}

.clickable {
  cursor: pointer;
}

.clickable:hover {
  opacity: 0.5;
}

.current-move {
  opacity: .8;
  font-size: 18px;
  outline: 6 px solid red;
  color: white;
  font-weight: bold;
}

.secondary {
  background-color: #e691cd;
}

.select-cell {
  display: inline-block;
  margin: 2px;
  border-radius: 4px;
  cursor: pointer;
}

.select-cell:hover {
  opacity: 0.5;
}